import React, { Component } from 'react';

export default function authenticated(WrappedComponent){
    return class extends Component {
        constructor(props) {
            super(props);
            let accessToken = localStorage.getItem('accessToken');
            this.state= { 
                accessToken: accessToken,
                isAuthenticated: !!accessToken
            };
            this.onChange = this.onChange.bind(this);
        }

        onChange(event) {
            localStorage.setItem('accessToken', event.target.value);
            this.setState({
                accessToken:event.target.value,
                isAuthenticated:!!event.target.value
            })
        }

        render() {
            return this.state.isAuthenticated ? <WrappedComponent accessToken={this.state.accessToken}/> : (
                <div>Not Authenticated, please enter <a href="https://github.com/settings/tokens">Github accessToken</a>!
                <input onChange={this.onChange}/>
                </div>)
        }
    }
}


