/** Radix tree implementation should be here */
export default class RadixTree {
    constructor (text, id=undefined){
        this.children = [];
        this.ids = id ? [id] : [];
        this.text = text;
    }

    /**
     * @param {String} text - string to be inserted into radix tree (if necessary)
     * @param {String} id - this id will be appended to the given node
     */
    append(text, id, root=this) {
        // for each child
        for(let i=0;i<this.children.length;i++){
            let child = this.children[i];
            //  if someone equals add id to that
            if(child.text === text) {
                child.appendId(id);
                return root;
            }
            //  if someone is a prefix of us hop on that
            if(child.text.length < text.length &&
                text.indexOf(child.text) === 0) {
                child.append(text.substring(child.text.length,text.length), id, this);
                return root;
            }
            //  if someone has a common part with text, split
            let j=0;
            for(j=0; j<text.length && j<child.text.length && text[j]===child.text[j]; j++);
            if(j>0){
                this.splitChild(i, j, text, id);
                return root;
            } 
        }
        // if we could not found add a new children
        this.children.push(
            new RadixTree(text, id)
        );

        return root;
    }

    appendId(id) {
        if(!this.ids.includes(id)) this.ids.push(id);
    }

    splitChild(childIndex, prefixEnd, text, id) {
        // replace the childIndex with a new RadixTree following the rules:
        //   - insert new node with empty ids
        //      - append old child to it
        //          - cut prefix out from old child's text
        //      - append a new child with the suffix and id
        let newPrefixTreeItem = new RadixTree(text.substring(0,prefixEnd));
        let oldChild = this.children[childIndex];
        oldChild.text = oldChild.text.substring(prefixEnd,oldChild.text.length);
        newPrefixTreeItem.children.push(oldChild);
        this.children[childIndex] = newPrefixTreeItem;
        let remainText = text.substring(prefixEnd,text.length);
        if(remainText) {
            let newSufixTreeItem = new RadixTree(remainText,id);
            newPrefixTreeItem.children.push(newSufixTreeItem);
        } else {
            newPrefixTreeItem.appendId(id);
        }
    }

    hasPrefix(text) {
        return this.text.indexOf(text) === 0;
    }

    isSame(text) {
        return this.hasPrefix(text) && this.text.length === text.length;
    }

    remove(text,id) {
        throw Error("Not implemented! Please rebuild tree instead");
    }

    /**
     * Finds the node of string and returns corresponding ids
     * @param {} text - String to llokup
     */
    search(text) {
        let j, child;
        // if child's text equals, return [ids]
        // if one child contains the text's prefix,
        //   cut the text and hop on it
        // for each child
        for(let i=0;i<this.children.length;i++){
            child = this.children[i];
            
            if(text===child.text)
                return child.subIds();

            for(j=0; j<text.length && j<child.text.length && text[j]===child.text[j]; j++);
            if(j===child.text.length) {
                // we can hop on exact match
                return child.search(text.substring(child.text.length, text.length));
            }
            if(j===text.length) {
                return child.subIds();
            }
        }

        return [];
    }

    subIds() {
        let retArr = cloneArray(this.ids);
        for(const child of this.children) retArr = mergeArrays(retArr, child.subIds());
        return retArr;
    }

    size() {
        let s = this.ids.length;
        for(const child of this.children) s+= child.size();
        return s;
    }
}

function cloneArray(arr) {
    return arr.slice();
}

function mergeArrays(...arr) {
    const retArr = [];

    for(const ar of arr) {
        for(const el of ar) {
            if(!retArr.includes(el)) retArr.push(el);
        }   
    }
    return retArr;
}


