import React, { Component } from 'react';
import './Gists.css';
import Gist from './Gist';
import GistContent from './content/Content';
import Search from './search/Search';
import ForgetToken from './ForgetToken';

import { Indexer, getMatches } from './indexer/Indexer';
import { fetchFile } from './content/File'

export default class Gists extends Component {
    constructor(props) {
        super(props);
        this.state = {
            gists: [],
            selectedGist: {}
        };

        this.showContent = this.showContent.bind(this);
        this.filter = this.filter.bind(this);
        this.clearFilter = this.filter.bind(this, null);
        this.getDocuments = this.getDocuments.bind(this);

        this.documentsReady = this.loadGists('https://api.github.com/gists?per_page=100')
            .catch((err) => {
                console.warn(err);
                this.setState({error:err});
            });
    }

    render() {
        let gistComponents = this.state.gists.map(gist =>
            <Gist key={gist.id}
                showContent={this.showContent}
                {...gist} />
        );
        let largeScreenContent = "";
        if(window.innerWidth>1100) {
            largeScreenContent = <GistContent key={this.state.selectedGist.id} {...this.state.selectedGist} />;
        }

        return (
            <div className="gists">
                <header>
                    <span>Gists
                    <a className="small-font" href="https://gist.github.com/" target="new-gist">Add</a>
                    </span>
                    <Search filter={this.filter} clear={this.clearFilter} getMatches={getMatches} />
                    <Indexer documentsReady={this.documentsReady} documents={this.getDocuments()} />
                    <ForgetToken />
                </header>
                { this.state.error ? this.state.error.toString() : ''}
                <div className="gists-display-area">
                    <div className="gists-left-pane">
                        <ol className="gist-list left">
                            {gistComponents}
                        </ol>
                    </div>
                    <div className="gists-right-pane">
                        {largeScreenContent}
                    </div>
                </div>
            </div>
        );
    }

    getDocuments() {
        const gists = this.state.gists;
        return {
            *[Symbol.iterator]() {
                for (let i = 0; i < gists.length; i++) {
                    let gist = gists[i];
                    for (let fileName in gist.files) {
                        yield loadDocument(gist, fileName);
                    }
                }
            }
        }

        async function loadDocument(gist, fileName) {
            try {
                //debugger
                const content = await fetchFile(gist.files[fileName]);
                return { id: gist.id, content };
            } catch (exc) {
                debugger
                return { id: -1, content: '' };
            }
        }
    }

    filter(matchingIds) {
        this.setState((prevState, props) => {
            let updatedGists = prevState.gists.map((gist) => {
                if (!matchingIds || matchingIds.includes(gist.id)) {
                    gist.visible = true;
                } else {
                    gist.visible = false;
                }
                gist.opened = false;
                return gist;
            });
            return {
                gists: updatedGists,
                selectedGist: {}
            }
        });
    }

    showContent(selectedGist) {
        this.setState((prevState, props) => {
            let gists = prevState.gists.map(gist => {
                gist.opened = gist.id === selectedGist.id;
                return gist;
            });

            return {
                gists: gists,
                selectedGist: selectedGist
            }
        });
    }

    async loadGists(apiUrl) {
        let headers = new Headers();
        headers.append('Authorization', 'token ' + this.props.accessToken);
        return fetch(apiUrl, { headers })
            .then(response => {
                if(response.status!==200) throw new Error("Invalid Token!");
                this.fetchNextPage(response);
                return response.json();
            })
            .then(gists => {
                let gistsFromResponse = gists.map(gist => {
                    gist.key = gist.id;
                    gist.showContent = this.showContent;
                    gist.visible = true;
                    return gist;
                });
                this.setState((prevState, props) => {
                    return {
                        gists: prevState.gists.concat(gistsFromResponse),
                        selectedGist: prevState.selectedGist
                    }
                });
            });
    }

    fetchNextPage(response) {
        let link = response.headers.get('Link'),
            nextPageMatch = link.match(/<([^<>]+)>; rel="next"/);

        if (nextPageMatch) {
            this.loadGists(nextPageMatch[1]);
        }
    }
}


