import React, { Component } from 'react';
import './Gist.css';
import Details from './Details';

export default class Gist extends Component {
    title;
    thumbnail;

    constructor(props) {
        super(props);
        this.parse();
        this.toggleOpened = this.toggleOpened.bind(this);
    }

    parse() {
        this.title = this.props.description || this.props.id;
    }

    toggleOpened() {
        this.props.showContent(this.props);
    }

    render () {      
        return (
            this.props.visible ? 
            <li className={`gist ${this.props.opened ? 'opened' : ''}`}>
                <span className="gist-title" onClick={this.toggleOpened}>
                    {this.title}
                </span>
                {this.props.opened && <Details {...this.props}/>}
            </li>
            :
            null
        )
    }
}


