import React, { Component } from 'react';
import './App.css';
import Gists from './gist/Gists';
import authenticated from './GithubAutheticated';

class App extends Component {
  render() {
    const AuthenticatedUserGists = authenticated(Gists);
    return (
      <div className="App">
        <AuthenticatedUserGists/>
      </div>
    );
  }
}

export default App;



