import React, { Component } from 'react';
//import './Details.css';
import FileView from './File';

export default class Content extends Component {
    render() {
        const className = [this.props.className,"content"].join(' ');
        let fileViewComponents = [];
        for( let fileName in this.props.files) {
            let file = this.props.files[fileName];
            fileViewComponents.push(<FileView key={fileName} {...file}/>);
        }
        return (
            <div className={className}>
                <h2>{this.props.description}</h2>
                {fileViewComponents}
            </div>);
    }
}


