import React, { Component } from 'react';
import './Indexer.css';
import RadixTree from './RadixTree';

let radixTree = new RadixTree();

export class Indexer extends Component {

    constructor(props) {
        super(props);
        this.state = { size: radixTree.size() }
        this.onStartIndexing = this.onStartIndexing.bind(this);
        this.props.documentsReady.then(()=>{
            this.onStartIndexing();
        });
    }

    render() {
        return <span className="indexer">
            <div>Index Size:{this.state.size}</div>
            <div className="hidden"><button onClick={this.onStartIndexing}>Start indexing</button></div>
        </span>;
    }

    onStartIndexing() {
        for (let onDocumentLoaded of this.props.documents) {
            onDocumentLoaded.then(({ id, content }) => {
                if (content) {
                    const matches = content.match(/\w{3,}/g);
                    if (matches) for (let word of matches) {
                        radixTree.append(word.toLowerCase(), id);
                    }
                }
                this.setState({ size: radixTree.size() });
            });
        }
    }
}

export function getMatches(filterCriteria) {
    return radixTree.search(filterCriteria);
}


