import React, { Component } from 'react';

export default class ForgetToken extends Component {
    constructor(props) {
        super(props);
        this.accessToken = localStorage.getItem('accessToken');
        this.clearToken = this.clearToken.bind(this);
        this.state = { isAuthenticated: !!this.accessToken};
    }

    clearToken(event) {
        //localStorage.removeItem('accessToken');
        //clearEverything!
        localStorage.clear();
        this.setState({ isAuthenticated: false});
        window.location.reload();
    }

    render() {
        return this.state.isAuthenticated ? <button onClick={this.clearToken}>Clear!</button> : null;
    }
}


