import React, { Component } from 'react';

export default class File extends Component {
    constructor(props) {
        super(props);
        this.state = {
            content: fetchFileContentFromLocalStorage(props)
        }
        this.reload = this.reload.bind(this);
    }

    componentDidMount() {
        fetchMissingFileContents(this.props, this.state.content)
            .then(data=>this.setState({content: data}));
    }

    reload() {
        fetchMissingFileContents(this.props)
            .then(data=>this.setState({content: data}));
    }

    render() {
        return (
            <div>
                <h3>
                    <a href={this.props.raw_url}>{this.props.filename}</a>
                    <button onClick={this.reload}>Reload</button>
                </h3>
                { isTextFile(this.props)  ?
                    <pre>{`${this.state.content}`}</pre> :
                    `Not text : ${this.props.type}`
                }
            </div>);
    }
}

export async function fetchFile(file) {
    let content = fetchFileContentFromLocalStorage(file);
    return await fetchMissingFileContents(file, content);
}

function isTextFile(file) {
    return ['text/css', 'text/html', 'text/plain', 'application/x-sh', 'application/javascript', 'application/json', 'application/x-ruby'].includes(file.type);
}

function fetchFileContentFromLocalStorage(file) {
    let content = localStorage.getItem(file.raw_url);
    if(content) {
        console.log('Cache hit: ', file.raw_url);
        return content;
    }
}

async function fetchMissingFileContents(file, content) {
    if(isTextFile(file) && !content) {
        console.log('Downloading ' + file.raw_url);
        localStorage.removeItem(file.raw_url);
        return await fetch(file.raw_url)
            .then(response=>{
                return response.text();
            })
            .then(data=>{
                localStorage.setItem(file.raw_url, data);
                return data;
            });
    }
    return content;
}


