import React, { Component } from 'react';
import './Details.css';

import GistContent from './content/Content';

export default class Details extends Component {
    constructor(props) {
        super(props);
        this.parse();
    }

    parse() {
        console.log(this.props);
        
        this.thumbnailFile = this.props.files["thumbnail.png"];
        if(this.thumbnailFile) {
            this.thumbnail = <img src={this.thumbnailFile.raw_url} alt={this.title}/>;
            this.thumbnailBg = {
                backgroundImage: `url(${this.thumbnailFile.raw_url})`
            };
        }
    }

    render(){
        let smallScreensContentView="";
        if(window.innerWidth<1100) {
            smallScreensContentView =  <GistContent key={this.props.id} {...this.props} />;
        }
        return <div className="details">
            <div className="gist-icon">
                {this.thumbnail}
            </div>
            <a href={this.props.html_url} alt={this.props.description}>{this.props.html_url}</a>
            {smallScreensContentView}
            </div>;
    }
}


